.login-body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
  
}

.login-body .container {
  display: flex;
  height: 100vh;
}

/* Left Side */
.login-body .left-side {
  position: relative;
  width: 66%;
  overflow: hidden;
}

.login-body .background-video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  opacity: 0.7;
  z-index: -1; /* Puts the video behind the content */
}

.login-body .left-side .content {
  position: relative;
  z-index: 1;
  padding-left: 160px;
  color: white;
  padding-top: 240px;
}

.login-body .left-side h1 {
  font-size: 2.0rem;
  color: var(--Dark-Blue, #24223e);
  font-family: "Quicksand", sans-serif;
}

.login-body .left-side h2 {
  font-size: 3.2rem;
  margin: 10px 0;
  color:#102039;
  font-family: "Quicksand", sans-serif;
}

.vcodeLogin{
  font-family: "Fira Sans", serif;
  color: var(--Dark-Blue, #24223e);
}

.letterVlogin{
  background: linear-gradient(99deg, #45c4f9, #7d09ff 50.33%, #ff0be5);
 -webkit-background-clip: text;
 -webkit-text-fill-color: transparent;
}

.vcodeLogin:hover{
  background: linear-gradient(99deg, #45c4f9, #7d09ff 50.33%, #ff0be5);
 -webkit-background-clip: text;
 -webkit-text-fill-color: transparent;
 .letterVlogin{
  background: var(--Dark-Blue, #24223e);
 -webkit-background-clip: text;
 -webkit-text-fill-color: transparent;
}
}

.login-body .left-side p {
  font-size: 1.5rem;
  color:#24223e;
  font-family: "Quicksand", sans-serif;
}



/* Right Side */
.login-body .right-side {
  width: 34%;
  padding: 40px;
  padding-left: 100px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background: linear-gradient(143deg, #FFFFFF 10%, #a2bcdf);
}

.login-body .right-side h1 {
  font-size: 2.8rem;
  color: var(--Dark-Blue, #24223e);;
  font-family: "Fira Sans", serif;
  font-style: medium;
}

.login-body .right-side h2 {
  font-size: 1.8rem;
  font-family: "Quicksand", sans-serif;
  color: #6087b9;
}

.login-body .right-side p {
  font-size: 1.2rem;
  font-family: "Quicksand", sans-serif;
  color: #4871a7;
}

.login-body .login-form {
  margin: 20px 0;
}

.login-body .input-field {
  width: 90%;
  padding: 10px;
  margin: 10px 0;
  border: 1px solid #a2bcdf;
  font-family: "Quicksand", sans-serif;
  border-radius: 4px;
  font-size: 16.5px;
  color:#000;
}

.login-body .input-field::placeholder {
  color: #939393; /* Set your desired placeholder color here */
  opacity: 1; /* Optional: to ensure the placeholder is fully opaque */
}

.login-body .input-field:focus {
  outline: none; /* Remove the outline on focus */
  
}

.login-body .options {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.login-body .options a {
  color: #888;
  text-decoration: none;
}

.login-body .login-button {
  width: 95%;
  padding: 10px;
  background-color: #e4eefc;
  color: #4871a7;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16.5px;
  transition: background-color 0.3s;
  box-shadow: 0 4px 4px 0 rgba(13, 21, 48, .04);
}

.login-body .login-button:hover{
  background-color: #4871a7;
  color: #e4eefc;
}

.login-body .signup {
  text-align: center;
  font-family: "Quicksand", sans-serif;
}

.login-body .signup a {
  color: #4871a7;
  font-weight: bold;
}

