  .signup-container {
    display: flex;
    min-height: 100vh;
    margin: 0;
    padding: 0;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  }
  
  /* Left Side */
  .signup-left-side {
    position: relative;
    width: 66%;
    overflow: hidden;
  }
  
  .signup-background-video {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    opacity: 0.8;
    z-index: -1; /* Puts the video behind the content */
  }
  
  .signup-left-side .signup-content {
    position: relative;
    z-index: 1;
    padding-left: 190px;
    color: white;
    padding-top: 240px;
  }
  
  .signup-left-side h1 {
    font-size: 2.0rem;
    color: #102039;
  }
  
  .signup-left-side h2 {
    font-size: 3.2rem;
    margin: 10px 0;
    color: #102039;
  }
  
  .signup-left-side p {
    font-size: 1.4rem;
    color: #084349;
    font-family: Titillium Web;
  }
  
  /* Right Side */
  .signup-right-side {
    width: 34%;
    padding: 40px;
    padding-left: 100px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-color: #466287;
  }
  
  .signup-right-side h1 {
    font-size: 2.2rem;
    color: #F7FBFD;
    font-family: Amaranth;
  }
  
  .signup-right-side h2 {
    font-size: 1.4rem;
    color: #F7FBFD;
  }
  
  .signup-right-side p {
    font-size: 1.0rem;
    color: #F7FBFD;
    font-family: Titillium Web;
  }
  
  .signup-form {
    margin: 20px 0;
  }
  
  .signup-input-field {
    width: 90%;
    padding: 10px;
    margin: 10px 0;
    border: 1px solid #a4a6ad;
    border-radius: 4px;
    background-color: #455a79b1;
    color: #bfc0c6;
  }
  
  .signup-input-field::placeholder {
    color: #a4a6ad; /* Placeholder color */
    opacity: 1; /* Full opacity */
  }
  
  .signup-input-field:focus {
    outline: none; /* Remove the outline on focus */
  }
  
  .signup-button {
    width: 95%;
    padding: 10px;
    background-color: #12bac974;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 15px;
  }
  
  .signup-button:hover {
    background-color: #102039;
  }
  
  .signup-login {
    text-align: center;
    font-family: Titillium Web;
  }
  
  .signup-login a {
    color: #bbbcbe;
    text-decoration: none;
    font-weight: bold;
  }
  